import { Grid } from '@mui/material'
import { Container } from '@mui/system'
import React, { useState } from 'react'
import { Sidebar } from '../sidebar/Sidebar'
import { sidebarAdvs } from '../../assets/data'
import { Link } from 'react-router-dom'

export const ContentScheme = ({sidebar=false, children}) => {

  return (
    <>
    <Container>
      <Grid container spacing={1}>
        
        {sidebar ?
        <Sidebar>
          {sidebarAdvs.map((adv, i)=>(
            <div key={i}
            style={{
              'text-align':'center'
            }}
            width='100%'
            >
              <Link to={adv.link} >
                <img src={adv.img} alt="" width='60%'/>
              </Link>
            </div>
          ))}
        </Sidebar>
          : null}
        
        <Grid item xs={sidebar ? 9 : 12} >
            {children}
        </Grid>
      </Grid>
    </Container>
    </>
  )
}
